<template>
    <div class="add-brand">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: 'Section'}"  class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div> 


      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-8">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add New Section</h3>
                  </div>
                </div>
              </div>
            </div>
          <form> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Title
                          </label>
                          <select class="form-control form-control-alternative" v-model="section_title">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in Object.keys(section_datas)"
                              :key="item"
                              :value="item"
                            >
                              {{ section_datas[item] }}
                            </option>
                          </select>
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Section Name
                          </label>
                          <select class="form-control form-control-alternative" v-model="section_name">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in Object.keys(section_names)"
                              :key="item"
                              :value="item"
                            >
                              {{ section_names[item] }}
                            </option>
                          </select>
                       </div>
                    </div>
                  </div>
                </div>
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Section Type
                          </label>
                          <select class="form-control form-control-alternative" v-model="section_type">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in Object.keys(section_types)"
                              :key="item"
                              :value="item"
                            >
                              {{ section_types[item] }}
                            </option>
                          </select>
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Status
                          </label>
                          <select class="form-control form-control-alternative" v-model="section_status">
                            <option selected value=true>Active</option>
                            <option value=false>Inactive</option>
                          </select>
                       </div>
                    </div>
                  </div>
                </div>
                <div class="">
                    <div class="row">
                        <div class="col-4">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Section Image
                          </label>
                          <div class="card">
                            <div v-if="image_url">
                              <img class="card-img-top" :src="image_url" alt="image ..1"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="file" type="file" v-on:change="handleImageUpload()" accept="image/*"/>
                              <button v-if="!image_url" class="btn btn-primary btn-sm" @click="$refs.file.click()">Add</button>
                              <button v-else class="btn btn-primary btn-sm" @click="$refs.file.click()">Change</button>
                              <button :disabled="!image_url" class="btn btn-info btn-sm" @click="removeImage()">Remove</button>
                            </div>
                          </div>
                       </div>
                    </div>
                    </div>
                </div>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="resetForm()">Cancel</button>
                    <button type="button" class="btn btn-info">Add Section</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
  .card-img-top{
    max-height: 172px;;
  }
  .card-img-top{
    min-height: 172px;;
  }
</style>
<script>
export default {
    name: "add Section",
    data(){
        return{
            image_url:'',
            section_datas:[],
            section_types:[],
            section_names:[],
            section_title:'',
            section_type:'',
            section_name:'',
            section_status:true
        }
    },
    async created(){
      this.section_datas = this.$store.state.section_datas

      this.section_types = this.$store.state.section_types

      this.section_names = this.$store.state.section_names
    },
    methods: {
        handleImageUpload(){
         let uploadedImage = this.$refs.file.files;
         console.log(uploadedImage)
         this.image_url = URL.createObjectURL(uploadedImage[0]);
        },
        removeImage(){
            this.image_url = "";
        }
    }
}
</script>