<template>
  <div class="edit-product">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
         <div class="col-md-12 pl-3">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: 'DistributionPoint'}" class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div>

      <div class="row no-gutters justify-content-center mt-1 mb-6">
        <div class="col-12">


          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add DistributionPoint</h3>
                  </div>
                </div>
              </div>
            </div>

            <div class="card-body">
              <form @submit="addDP()"> 
                <h6 class="heading-small text-muted mb-4">DP Contact Information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                        <div class="form-group has-label">
                          <label class="form-control-label">
                             DP ID
                          </label>
                            <input
                              placeholder="DP ID"
                              id="dpId"
                              v-model="dpID"
                              class="form-control form-control-alternative"
                              required
                              type="text"
                            />
                        </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               DP Owner
                            </label>
                            <input
                              placeholder="DP Owner"
                              id="dpName"
                              v-model="dpName"
                              required
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Mobile Number
                            </label>
                            <input
                              placeholder="Mobile Number"
                              id="mobileNumber"
                              required
                              v-model="mobileNumber"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Alt Mobile Number
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="altMobNum"
                              v-model="altMobNumber"
                              placeholder="Alt Mobile Number"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                              Email Address
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="emailAddress"
                              placeholder="Email Address"
                              required
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">DP Address Information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               DP Address
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="dpAddress"
                              placeholder="DP Address"
                              required
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               City
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="cityName"
                              placeholder="City Name"
                              required
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               State
                            </label>
                            <input
                              placeholder="State"
                              v-model="state"
                              required
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               PinCode
                            </label>
                            <input
                              placeholder="PinCode"
                              v-model="pincode"
                              required
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">DP Status</h6>
                <div class="">
                  <div class="row">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label">
                              Status
                           </label>
                            <select class="form-control form-control-alternative" v-model="Status">
                              <option value="true">Active</option>
                              <option value="false">InActive</option>
                            </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </form>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-outline-primary product-btn-cancel" @click="resetForm()">Cancel</button>
                    <button
                      type="submit"
                      v-on:click="addDP()"
                      class="btn btn-info"
                      >Add DP</button
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input, select{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
import axios from "axios";
axios.defaults.xsrfHeaderName = "X-CSRFToken";

export default {
    name: 'AddDistributionPoint',
    data() {
        return {
            dpID:"",
            dpName: "",
            dpAddress:"",
            mobileNumber:"",
            altMobNumber:"",
            cityName:"",
            state:"",
            pincode:"",
            Status:true,
        }
    },
    methods:{
        async addDP(){
            const { dpID, dpName, dpAddress, mobileNumber, emailAddress, altMobNumber, cityName, state, pincode, Status} = this;
            if(!dpID || !dpName || !dpAddress || !mobileNumber || !cityName || !state || !pincode || !emailAddress){
                this.$toast.error(`Please fill all the required field`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
            var data = JSON.stringify({
                "dpID": dpID,
                "dpName": dpName,
                "dpAddress": dpAddress,
                "cityName": cityName,
                "pincode": pincode,
                "state": state,
                "mob": mobileNumber,
                "alternative_mob": altMobNumber,
                "emailAddress": emailAddress,
                "status": Status
            });

            var config = {
            method: 'post',
            url: process.env.VUE_APP_SERVER_HOST+'/api/distributionpoint/',
            headers: { 
                'Content-Type': 'application/json', 
            },
            data : data,
            xsrfHeaderName: "X-CSRFToken",
            };

           let res = await axios(config)
           if(res.status == 201){
               this.$toast.success(`Distribution Point added successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            this.resetForm();
           }else{
               this.$toast.error(`Something went wrong`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
           }
           
        },
        resetForm(){
            this.dpID="";
            this.dpName= "";
            this.dpAddress="";
            this.mobileNumber="";
            this.altMobNumber="";
            this.cityName="";
            this.state="";
            this.pincode="";
        },
    }
}
</script>