<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            User List
          </h3>
        </div>
        <div class="form-group mb-0 col-6 mt-md-2">
          <div class="form-group input-group-alternative input-group" style="border: none; margin-bottom: 0px; border-radius: 58px" >
            <input
              aria-describedby="addon-right addon-left"
              placeholder="Search User With UserID, Name, email, Address"
              v-on:keyup.enter="searchTable()"
              class="form-control search-box"
              v-model="searchQuery"
            />

          </div>
        </div>
        <div class="col text-right">
          <base-button type="info" @click="addProduct()" size="sm">Add User</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData.results"
      >
        <template v-slot:columns>
          <th>User Unique Id</th>
          <th>First Name</th>
          <th>Last Name</th>
          <th>Mobile Number</th>
          <th>Email</th>
          <th>Address</th>
          <th>Profile Picture</th>
          <th>Status</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">{{ row.item.userId }}</span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.first_name }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.last_name }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.mob }}
            </div>
          </td>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.email }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.address }}
            </div>
          </td>
           <td>
            <div class="avatar-group">
              <a
                href="#"
                class="avatar avatar-sm rounded-circle"
                data-toggle="tooltip"
                data-original-title="Jessica Doe"
              >
                <img
                  alt="Image placeholder"
                  class="rounded-circle"
                  :src="row.item.profile_picture"
                />
              </a>
            </div>
          </td>
           <td class="text-align-center">
            <div v-if="row.item.is_active" class="text-success">Active</div>
            <div v-else class="text-danger">InActive</div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>

      <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
       <ul class="pagination">
        <li class="text-right pr-3">
          <button
            type="button"
            :disabled="!tableData.previous"
            @click="getPrevData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
        </li>
        <li class="text-center" style="max-width: 55px">
          <span class="page-number-circle">{{ pagination.default }}</span>
        </li>
        <li class="text-right pl-3">
          <button
            type="button"
            :disabled="!tableData.next"
            @click="getNextData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </li>
      </ul>

    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  name: "user-table",
  props: {
    type: {
      type: String,
    },
    title: String,
  },
  data() {
    return {
      tableData: [],
      perpage:8,
      currentpage:1,
       pagination: {
        default: 1
      },
      searchQuery:"",
    };
  },
  async mounted() {
    let user_lists = await axios.get(process.env.VUE_APP_SERVER_HOST+'/api/user/');
    this.tableData = user_lists.data;
  },
  methods:{
      addUser(){
        alert("Work in Progress")
      },
      async searchTable(){
         let user_lists = await axios.get(process.env.VUE_APP_SERVER_HOST+`/api/user/?search=${this.searchQuery}`);
          if(user_lists.data.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = user_lists.data;
      },
      async getNextData(){
          let next = this.tableData.next;
          this.tableData.next=null;
          ++this.pagination.default;
          if(next){
            let products = await axios.get(next);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
          }
        },
      async getPrevData(){
        let prev = this.tableData.previous;
        this.tableData.previous = null
        --this.pagination.default;
        if(prev){
          let products = await axios.get(prev);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
        }
      }
  }
};
</script>
<style></style>
