<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Distribution Point
          </h3>
        </div>
        <div class="form-group mb-0 col-6 mt-md-2">
          <div class="form-group input-group-alternative input-group" style="border: none; margin-bottom: 0px; border-radius: 58px" >
            <input
              aria-describedby="addon-right addon-left"
              placeholder="Search Ditribution Point With DPID, Name, State, City or Address"
              v-on:keyup.enter="searchTable()"
              class="form-control search-box"
              v-model="searchQuery"
            />

          </div>
        </div>
        <div class="col text-right">
          <base-button type="info" @click="addDistributionPoint()" size="sm">Add Distribution Point</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData.results"
      >
        <template v-slot:columns>
          <th>DP ID</th>
          <th>DP Owner</th>
          <th>DP Address</th>
          <th>Mob</th>
          <th>Email</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                  <router-link :to="{name: 'ViewDistributionpoint', params:{id:row.item.id}}" >{{row.item.dpID }} </router-link>
                </span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.dpName }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <div class="">{{ row.item.dpAddress }}</div>
                <div class="">City - {{ row.item.cityName }}, State - {{ row.item.state}}</div>
                <div class="">PinCode - {{ row.item.pincode }}</div>
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.mob }}
            </div >
            <div v-if="row.item.alternative_mob">
              Alt Mob - {{ row.item.alternative_mob}}
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.emailAddress" class="align-center">
                {{ row.item.emailAddress }}
            </div>
            <div v-else class="align-center">Not given*</div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:'ViewDistributionpoint', params:{id: row.item.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteDP(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
       <ul class="pagination">
        <li class="text-right pr-3">
          <button
            type="button"
            :disabled="!tableData.previous"
            @click="getPrevData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
        </li>
        <li class="text-center" style="max-width: 55px">
          <span class="page-number-circle">{{ pagination.default }}</span>
        </li>
        <li class="text-right pl-3">
          <button
            type="button"
            :disabled="!tableData.next"
            @click="getNextData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </li>
      </ul>

    </div>
  </div>
</template>
<script>
import axios from "axios";
axios.defaults.xsrfHeaderName = "X-CSRFToken";
export default {
  name: "dp-table",
  props: {
    type: {
      type: String,
    },
    title: String,
  },
  data(){
      return {
          tableData: [],
          searchQuery:"",
           pagination: {
            default: 1
          },
      }
  },
  async mounted() {
      let d_points = await axios.get(process.env.VUE_APP_SERVER_HOST+"/api/distributionpoint/");
      this.tableData = d_points.data;
  },
  methods: {
    addDistributionPoint(){
      this.$router.push('/add/distributionpoint');
    },
    async deleteDP(dp_id){
      this.$swal({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) =>{
        if(result.isConfirmed) {
           var config = {
              method: 'delete',
              url: process.env.VUE_APP_SERVER_HOST+'/api/distributionpoint/'+ dp_id +'/',
              xsrfHeaderName: "X-CSRFToken",
            };
            axios(config).then(() =>{
              this.$swal('Deleted!','Your file has been deleted.','success');
            }).catch((e) =>{
              this.$swal('Something went wrong!',e.message,'danger');
            })
        } 
      })
    },
    async searchTable(){
      let d_points = await axios.get(process.env.VUE_APP_SERVER_HOST+`/api/distributionpoint/?search=${this.searchQuery}`);
      if(d_points.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
          });
        }
      this.tableData = d_points.data;
    },
    async getNextData(){
          let next = this.tableData.next;
          this.tableData.next=null;
          ++this.pagination.default;
          if(next){
            let products = await axios.get(next);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
          }
        },
      async getPrevData(){
        let prev = this.tableData.previous;
        this.tableData.previous = null
        --this.pagination.default;
        if(prev){
          let products = await axios.get(prev);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
        }
      }
  }
}
</script>
