<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Sections
          </h3>
        </div>
         <div class="col text-right">
          <base-button type="info" size="sm" @click="addSection()">Add Section</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="sectionData"
      >
        <template v-slot:columns>
          <th>Section Name</th>
          <th>Section Type</th>
          <th>Section Data</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                  <router-link :to="{name:'ViewSection', params:{id:row.item.id}}" >{{ row.item.section_name }}</router-link>
                </span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
               {{ row.item.type }}
            </div>
          </td>

          <td class="text-align-center">
            <div class="align-center">
               <div v-for="data in mainData[row.item.section_name]" :key="data"> {{data.title}}</div>
            </div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <a
                class="dropdown-item"
                >Edit</a>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
      <base-pagination :page-count="10" v-model="pagination.default"></base-pagination>
    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
    name: 'SectionTable',
    props: {
      type: {
        type: String,
      },
      title: String,
    },
    data(){
        return {
            tableData:[],
            mainData:{},
            sectionData:[],
            perpage:8,
            currentpage:1,
            pagination: {
                default: 1
            }
        }
    },
    async mounted() {
        let sections = await axios.get(process.env.VUE_APP_SERVER_HOST+'/api/section/')
        let homeSection = {}
        sections.data.forEach((item)=>{
          if(item.section_name in homeSection){
            homeSection[item.section_name].push(item)
          }else{
            homeSection[item.section_name]=[]
            this.sectionData.push({'section_name':item.section_name,'id':item.id,'type':item.section_type})
            homeSection[item.section_name].push(item)
          }
        })
        // console.log(homeSection)
        this.tableData = sections.data;
        this.mainData = homeSection
    },
    methods: {
      addSection(){
        this.$router.push({name: "AddSection"})
      }
    }
}
</script>