<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center no-gutters">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Brands
          </h3>
        </div>
        <div class="col text-right">
          <base-button type="info" @click="addBrand()" size="sm">Add Brand</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Brand Name</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">{{ row.item.BrandName }}</span>
              </div>
          </th>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:'EditBrand', params:{id:row.item.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteBrand(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>

        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
      <base-pagination :page-count="10" v-model="pagination.default"></base-pagination>
    </div>
  </div>
</template>
<script>
import axios from "axios";
axios.defaults.xsrfHeaderName = "X-CSRFToken";

export default {
  name: "brands-table",
  props: {
    type: {
      type: String,
    },
    title: String,
  },
  data() {
    return {
      tableData: [],
      perpage:8,
      currentpage:1,
       pagination: {
        default: 1
      }
    };
  },
  async mounted() {
      let brands = await axios.get(process.env.VUE_APP_SERVER_HOST+"/api/brand/");
      this.tableData = brands.data;
  },
  async created(){
      this.checkStore();
  },
  methods:{
    addBrand(){
        this.$router.push('/addbrand');
    },
    checkStore(){
        console.log(this.$store.state)
    },
    async deleteBrand(brand_id){
      this.$swal({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) =>{
        if(result.isConfirmed) {
           var config = {
              method: 'delete',
              url: process.env.VUE_APP_SERVER_HOST+'/api/brand/'+ brand_id +'/',
              xsrfHeaderName: "X-CSRFToken",
            };
            axios(config).then(() =>{
              this.$swal('Deleted!','Selected BrandName has been deleted.','success');
              this.tableData = this.tableData.filter((obj)=>{return obj.id !== brand_id;})
            }).catch((e) =>{
              this.$swal('Something went wrong!',e.message,'danger');
            })
        } 
      })
    }
  }
};
</script>
<style></style>
