import { createStore } from 'vuex'
import axios from 'axios';

const store = createStore({
    state: {
        title: 'Vuex Store',
        section_datas: {
            'why_to_use':'Why to use',
            'demo_videos':'Demo Videos',
            'business_opportunity':'Business Opportunity',
            'training_videos':'Training Videos',
            'motivational_videos':'Motivational Videos',
            'success_stories':'Success Stories',
            'testimonial_videos':'Testimonial Videos',
            'about_modicare':'About Modicare',
            'become_a_partner':'Become a Partner',
            'previous_events':'Previous Events',
            'upcoming_events':'Upcoming Events',
            'book_ticket':'Book Ticket'
        },
        section_types: {
            'video': 'Video',
            'static': 'Static'
        },
        section_names: {
            'why_modicare':'Why Modicare',
            'learnings': 'Learnings',
            'archivers': 'Archivers',
            'events': 'Events',
        },
        authenticated: false,
        static_data:{},
    },
    getters:{
        static_data: (state) => state.static_data
    },
    mutations: {
        setAuthentication(state, status){
            state.authenticated = status;
        }, 
        SET_STATIC_DATA(state, data){
            state.static_data = data;
        },
        UPDATE_ABOUT_US(state, payload){
            let [key, value] = Object.entries(payload)[0];            
            state.static_data[key] = value;
        }

    },
    actions: {
        getStaticData({commit}){
            axios.get(process.env.VUE_APP_SERVER_HOST+`/api/staticdata/`)
            .then(response => {
                console.log("store",response.data)
                commit('SET_STATIC_DATA', response.data[0])
            })
        },
        addStaticData({commit}, payload){
            commit('UPDATE_ABOUT_US',payload)
        }
    }
})

export default store;