<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Products
          </h3>
        </div>
        <div class="form-group mb-0 col-4 mt-md-2">
          <div class="form-group input-group-alternative input-group" style="border: none; margin-bottom: 0px; border-radius: 58px" >
            <input
              aria-describedby="addon-right addon-left"
              placeholder="Search Product Code, Brand or Product Name"
              v-on:keyup.enter="searchTable()"
              class="form-control search-box"
              v-model="searchQuery"
            />

          </div>
        </div>
        <!-- <div class="col-4 mt-2"> -->
          <div class="col-2">
            <select @change="filterTable()" v-model="Loyalty" class="form-control filter-dropdown ml-2">
            <option value="">All Loyalty</option>
            <option value="true">Loyalty: True</option>
            <option value="false">Loyalty: False</option>
          </select>
          </div>
         <div class="col-2">
           <select @change="filterTable()" v-model="Status" class="form-control filter-dropdown ml-2">
            <option value="">All Status</option>
            <option value="true">Status: Active</option>
            <option value="false">Status: InActive</option>
          </select>
         </div>
        <!-- </div> -->

        <div class="col text-right">
          <base-button type="info" @click="addProduct()" size="sm">Add Product</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData.results"
      >
        <template v-slot:columns>
          <th>Product Code</th>
          <th>Product Name</th>
          <th>Product Price</th>
          <th>Brand Name</th>
          <th>Loyalty</th>
          <th>Status</th>
          <th>Quantity</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                  <router-link :to="{name:'ViewProduct', params:{id:row.item.id}}" >{{row.item.productCode }}</router-link>
                </span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.productName }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
               Rs. {{parseInt(row.item.productPrice).toFixed(2)}}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.brand.BrandName }}
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.loyalty" class="text-success">True</div>
            <div v-else class="text-danger">False</div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.status" class="text-success">Active</div>
            <div v-else class="text-danger">InActive</div>
          </td>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.quantity }}
            </div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:'ViewProduct', params:{id: row.item.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteProduct(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
       <ul class="pagination">
        <li class="text-right pr-3">
          <button
            type="button"
            :disabled="!tableData.previous"
            @click="getPrevData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
        </li>
        <li class="text-center" style="max-width: 55px">
          <span class="page-number-circle">{{ pagination.default }}</span>
        </li>
        <li class="text-right pl-3">
          <button
            type="button"
            :disabled="!tableData.next"
            @click="getNextData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </li>
      </ul>

    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "product-table",
  props: {
    type: {
      type: String,
    },
    title: String,
  },
  data() {
    return {
      tableData: [],
      perpage:1,
      currentpage:1,
       pagination: {
        default: 1
      },
      brand_lists:[],
      BrandID:"",
      Status:"",
      Loyalty:"",
      searchQuery:"",
    };
  },
  async mounted(){
      let products = await axios.get(process.env.VUE_APP_SERVER_HOST+"/api/products/");
      this.tableData = products.data;
  },
  methods:{
        addProduct(){
          this.$router.push('/addproduct');
        },
        async deleteProduct(product_id){
          this.$swal({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) =>{
            if(result.isConfirmed) {
              var config = {
                  method: 'delete',
                  url: process.env.VUE_APP_SERVER_HOST+'/api/products/'+ product_id +'/',
                  xsrfHeaderName: "X-CSRFToken",
                };
                axios(config).then(() =>{
                  this.tableData.results = this.tableData.results.filter((obj)=>{return obj.id !== product_id;});
                  this.$swal('Deleted!','selected Product has been deleted.','success');
                }).catch((e) =>{
                  this.$swal('Something went wrong!',e.message,'danger');
                })
            } 
          })
        },
        async searchTable(){
          let products = await axios.get(process.env.VUE_APP_SERVER_HOST+`/api/products/?search=${this.searchQuery}`);
          if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
        },
        async filterTable(){
          let loyalty = this.Loyalty;
          let status = this.Status;
          if(loyalty == "true"){loyalty = true}else if(loyalty == "false"){loyalty = false}
          if(status == "true"){status = true}else if(status == "false"){status = false}
          let products = await axios.get(process.env.VUE_APP_SERVER_HOST+`/api/products/?loyalty=${loyalty}&status=${status}`);
          if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
        },
        async getNextData(){
          let next = this.tableData.next;
          this.tableData.next=null;
          ++this.pagination.default;
          if(next){
            let products = await axios.get(next);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
          }
        },
      async getPrevData(){
        let prev = this.tableData.previous;
        this.tableData.previous = null
        --this.pagination.default;
        if(prev){
          let products = await axios.get(prev);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
        }
      }
  }
};
</script>
<style>
  .pagination {
        display: flex;
        padding-left: 0;
        list-style: none;
      }
      .pagination-btn {
        background-color: #e8e5e5;
        border: 0;
        padding: 10px 40px;
        border-radius: 61px;
      }
      .page-number-circle {
        display: inline-block;
        border-radius: 50%;
        min-width: 40px;
        min-height: 40px;
        text-align: center;
        padding: 8px;
        border: 1px solid #d6d6d6;
      }

</style>
