<template>
  <div class="edit-product">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
         <div class="col-md-12 pl-3">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: 'DistributionPoint'}" class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div>

      <div class="row no-gutters justify-content-center mt-1 mb-6">
        <div class="col-12">


          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">View/Upadate DistributionPoint</h3>
                  </div>
                </div>
              </div>
            </div>

            <div class="card-body">
              <form> 
                <h6 class="heading-small text-muted mb-4">DP Contact Information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                        <div class="form-group has-label">
                          <label class="form-control-label">
                             DP ID
                          </label>
                            <input
                              placeholder="DP ID"
                              v-model="dpID"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                        </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               DP Owner
                            </label>
                            <input
                              placeholder="DP Owner"
                              v-model="dpName"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Mobile Number
                            </label>
                            <input
                              placeholder="Mobile Number"
                              v-model="mobileNumber"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Alt Mobile Number
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="altMobNumber"
                              placeholder="Alt Mobile Number"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                              Email Address
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="emailAddress"
                              placeholder="Email Address"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">DP Address Information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               DP Address
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="dpAddress"
                              placeholder="DP Address"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               City
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="cityName"
                              placeholder="City Name"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               State
                            </label>
                            <input
                              placeholder="State"
                              v-model="state"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               PinCode
                            </label>
                            <input
                              placeholder="PinCode"
                              v-model="pincode"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>


                
                

                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">DP Status</h6>
                <div class="">
                  <div class="row">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label">
                              Status
                           </label>
                            <select class="form-control form-control-alternative" v-model="Status">
                              <option value="true">Active</option>
                              <option value="false">InActive</option>
                            </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </form>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <router-link type="primary" :to="{name:'DistributionPoint'}" class="btn btn-secondary mr-2"
                      >Cancel</router-link>
                    <button
                      type="primary"
                      class="btn btn-info"
                      v-on:click="updateDP()"
                      >Update DP</button
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input, select{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
import axios from "axios";
export default {
    name: "View/Update DP",
    data() {
        return {
            dpID:"",
            dpName: "",
            dpAddress:"",
            mobileNumber:"",
            altMobNumber:"",
            cityName:"",
            state:"",
            pincode:"",
            Status:true,
        }
    },
    async mounted(){
        let dp_id = this.$route.params.id;
        let dpDetail = await axios.get(process.env.VUE_APP_SERVER_HOST+`/api/distributionpoint/${dp_id}/`);
        this.dpID = dpDetail.data.dpID;
        this.dpName = dpDetail.data.dpName;
        this.dpAddress = dpDetail.data.dpAddress;
        this.mobileNumber = dpDetail.data.mob;
        this.altMobNumber = dpDetail.data.alternative_mob || "Not Given*";
        this.emailAddress = dpDetail.data.emailAddress;
        this.cityName = dpDetail.data.cityName;
        this.state = dpDetail.data.state;
        this.pincode = dpDetail.data.pincode;
        this.Status = dpDetail.data.status;
        console.log('host',process.env.VUE_APP_SERVER_HOST)
    },
    methods: {
      async updateDP(){
        const { dpID, dpName, dpAddress, mobileNumber, emailAddress, altMobNumber, cityName, state, pincode, Status} = this;
        var data = JSON.stringify({
          "dpID": dpID,
          "dpName": dpName,
          "dpAddress": dpAddress,
          "cityName": cityName,
          "pincode": pincode,
          "state": state,
          "mob": mobileNumber,
          "alternative_mob": altMobNumber,
          "emailAddress": emailAddress,
          "status": Status
        });

        var config = {
          method: 'put',
          url: process.env.VUE_APP_SERVER_HOST+`/api/distributionpoint/${this.$route.params.id}/`,
          headers: { 
            'Content-Type': 'application/json', 
          },
          data : data
        };
        let res = await axios(config);
        if(res.status == 200) {
          this.$toast.success(`Distribution Point updated successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
          });
        }
      }
    }
}
</script>