import { createRouter, createWebHistory } from "vue-router";

import DashboardLayout from "@/layout/DashboardLayout";
// import ShivDashLayout from "@/layout/ShivDashLayout";
import TeamAuthLayout from "@/layout/TeamAuthLayout";

import Dashboard from "../views/Dashboard.vue";
import Icons from "../views/Icons.vue";
import Maps from "../views/Maps.vue";
import Profile from "../views/UserProfile.vue";
import Tables from "../views/Tables.vue";

import Login from "../views/Login.vue";
import Register from "../views/Register.vue";
import TeamDash from '../views/TeamDash.vue';
import TeamLogin from '../views/TeamLogin.vue';
import Products from '../views/Products/Products';
import AddProduct from '../views/Products/AddProduct';
import ViewProduct from '../views/Products/ViewProduct.vue';
import Brands from '../views/Brand/Brands';
import AddBrand from '../views/Brand/AddBrand';
import EditBrand from '../views/Brand/EditBrand';
// import test from '../views/Brand/test';
import Categories from '../views/Category/Categories';
import AddCategory from '../views/Category/AddCategory';
import EditCategory from '../views/Category/EditCategory';
import DistributionPoint from '../views/DistributionPoint/DistributionPoint.vue';
import ViewDistributionpoint from '../views/DistributionPoint/ViewDistributionpoint.vue';
import AddDistributionPoint from '../views/DistributionPoint/AddDistributionPoint.vue';
import Users from '../views/Users/Users.vue';
import Orders from '../views/Order/Orders';
import OrderProduct from '../views/Order/OrderProduct';
import NotFound from '../views/notFound';
import Section from '../views/Section/Section';
import AddSection from '../views/Section/AddSection';
import ViewSection from '../views/Section/ViewSection';
import PrivacyPolicy from '../views/StaticPage/PrivacyPolicy';
import AboutUs from '../views/StaticPage/AboutUs';
import TermsAndConditions from '../views/StaticPage/TermsAndCondition';
import StaticContent from '../views/StaticContent/StaticContent';


const routes = [
  {
    path: "/",
    redirect: "/login",
    component: DashboardLayout,
    children: [
      {
        path: "/dashboard",
        name: "dashboard",
        components: { default: Dashboard },
      },
      {
        path: "/users",
        name: "Users",
        components: { default: Users },
      },
      {
        path: "/icons",
        name: "icons",
        components: { default: Icons },
      },
      {
        path: "/maps",
        name: "maps",
        components: { default: Maps },
      },
      {
        path: "/profile",
        name: "profile",
        components: { default: Profile },
      },
      {
        path: "/tables",
        name: "tables",
        components: { default: Tables },
      },
      {
        path: "/main-dashboard",
        name: "TeamDash",
        components: { default: TeamDash },
      },
      {
        path: "/products",
        name: "Products",
        components: { default: Products },
      },
      {
        path: "/addproduct",
        name: "AddProduct",
        components: { default: AddProduct },
      },
      {
        path: "/view/:id/product",
        name: "ViewProduct",
        components: { default: ViewProduct },
      },
      {
        path: "/brands",
        name: "Brands",
        components: { default: Brands },
      },
      {
        path: "/addbrand",
        name: "AddBrand",
        components: { default: AddBrand },
      },
      {
        path: ":id/editbrand",
        name: "EditBrand",
        components: { default: EditBrand },
      },
      {
        path: "/categories",
        name: "Categories",
        components: { default: Categories },
      },
      {
        path: "/addcategory",
        name: "AddCategory",
        components: { default: AddCategory },
      },
      {
        path: ":id/editcategory",
        name: "EditCategory",
        components: { default: EditCategory },
      },
      {
        path: "/distributionpoint",
        name: "DistributionPoint",
        components: { default: DistributionPoint },
      },
      {
        path: "/view/:id/distributionpoint",
        name: "ViewDistributionpoint",
        components: { default: ViewDistributionpoint },
      },
      {
        path: "/add/distributionpoint",
        name: "AddDistributionPoint",
        components: { default: AddDistributionPoint },
      },
      {
        path: "/order",
        name: "Orders",
        components: { default: Orders },
      },
      {
        path: "/order/:id/product",
        name: "OrderProduct",
        components: { default: OrderProduct },
      },
      {
        path: "/section",
        name: "Section",
        components: { default: Section },
      },
      {
        path: "/add/section",
        name: "AddSection",
        components: { default: AddSection },
      },
      {
        path: "/view/:id/section",
        name: "ViewSection",
        components: { default: ViewSection },
      },
      {
        path: "/static/content",
        name: "StaticContent",
        components: { default: StaticContent },
      },
    ],
  },
  {
    path: "/privacy-policy",
    name: "PrivacyPolicy",
    components: { default: PrivacyPolicy },
  },
  {
    path: "/about-us",
    name: "AboutUs",
    components: { default: AboutUs },
  },
  {
    path: "/terms-and-conditions",
    name: "TermsAndConditions",
    components: { default: TermsAndConditions },
  },
  {
    path: "/",
    redirect: "login",
    component: TeamAuthLayout,
    children: [
      {
        path: "/login2",
        name: "login",
        components: { default: Login },
      },
      {
        path: "/login",
        name: "TeamLogin",
        components: { default: TeamLogin },
      },
      {
        path: "/register",
        name: "register",
        components: { default: Register },
      },
    ],
  },
  {
    path: "/:catchAll(.*)",
    name:"NotFound",
    component: NotFound,
  },
];

const router = createRouter({
  mode: "history",
  history: createWebHistory(process.env.NODE_ENV === "production"?'/':'/'),
  linkActiveClass: "active",
  routes,
});

router.beforeEach(async (to, from, next) => {
  // console.log(to,from);
  let authenticated_routes = [
    "dashboard",
    "Users",
    "Brands",
    "Products",
    "Categories",
    "AddBrand"
  ];
  let unchecked_routes = ["NotFound"];
  if(authenticated_routes.find(o=>o===to.name)){ 
    next();
  }else if(!unchecked_routes.find(o=>o===to.name)){ 
    next();
  }

})

export default router;
