<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Order
          </h3>
        </div>
        <div class="form-group mb-0 col-6 mt-md-2">
          <div class="form-group input-group-alternative input-group" style="border: none; margin-bottom: 0px; border-radius: 58px" >
            <input
              aria-describedby="addon-right addon-left"
              placeholder="Search Order with Order Number"
              v-on:keyup.enter="searchTable()"
              class="form-control search-box"
              v-model="searchQuery"
            />

          </div>
        </div>
        <div class="col text-right">
          <!-- <base-button type="info" size="sm"></base-button> -->
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData.results"
      >
        <template v-slot:columns>
          <th>Order Number</th>
          <th>Order Date</th>
          <th>User</th>
          <th>DP Details</th>
          <th>Total Amount</th>
          <th>Status</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                    <router-link :to="{name:'OrderProduct', params:{id:row.item.id}}" >{{row.item.orderNumber }}</router-link>
                </span>
              </div>
          </th>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.created_at }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.user.email }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <div class="font-weight-bold"><a :href="'/#/view/' + row.item.distributionpoint.id + '/distributionpoint'" >{{ row.item.distributionpoint.dpID }}</a></div>
                <div class="">{{ row.item.distributionpoint.dpName }}</div>
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <div class="font-weight-bold">Rs. {{ calculateTotalamount(row.item.orderProducts) }}</div>
                <div v-if="calculateLength(row.item.orderProducts) === 1" class="font-italic">
                    ({{ calculateLength(row.item.orderProducts) }} Product)
                </div>
                <div v-else class="font-italic">
                    ({{ calculateLength(row.item.orderProducts) }} Products)
                </div>
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.status" class="text-success">Active</div>
            <div v-else class="text-danger">InActive</div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:'OrderProduct', params:{id:row.item.id}}"
                >View Order Details</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteOrder(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>

        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
       <ul class="pagination">
        <li class="text-right pr-3">
          <button
            type="button"
            :disabled="!tableData.previous"
            @click="getPrevData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
        </li>
        <li class="text-center" style="max-width: 55px">
          <span class="page-number-circle">{{ pagination.default }}</span>
        </li>
        <li class="text-right pl-3">
          <button
            type="button"
            :disabled="!tableData.next"
            @click="getNextData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </li>
      </ul>

    </div>
  </div>
</template>
<script>
import axios from "axios";
var axiosDefaults = require("axios/lib/defaults");
axiosDefaults.xsrfCookieName = "csrftoken"
axiosDefaults.xsrfHeaderName = "X-CSRFToken"
export default {
    name: 'orderTable',
    props: {
        type: {
            type: String,
        },
        title: String,
    },
    data() {
        return {
            tableData: [],
            orderProducts: {},
            totalPriceData: {},
            perPage:8,
            currentpage:1,
            pagination: {
              default: 1
            },
            searchQuery:""
        }
    },
    async mounted(){
      let orders = await axios.get(process.env.VUE_APP_SERVER_HOST+"/api/order/");
      this.tableData = orders.data;

  },
  methods: {
      calculateTotalamount(dataArr) {
          if(dataArr.length === 1){
              return ((parseFloat(dataArr[0].PricePerPiece)) * dataArr[0].productQty).toFixed(2)
          }else if(dataArr.length === 0){
              return;
          }
          else{
              let res = dataArr.reduce((acc, item)=>{
                return (parseFloat(acc.PricePerPiece) * acc.productQty) + (parseFloat(item.PricePerPiece) * item.productQty) 
            })
            return (res).toFixed(2);
          }  
      },
      calculateLength(dataArr){
          return dataArr.length;
      },
      async searchTable(){
        let orders = await axios.get(process.env.VUE_APP_SERVER_HOST+`/api/order/?search=${this.searchQuery}`);
        if(orders.data.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
          });
        }
        this.tableData = orders.data;
      },
      async getNextData(){
          let next = this.tableData.next;
          this.tableData.next=null;
          ++this.pagination.default;
          if(next){
            let products = await axios.get(next);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
          }
        },
      async getPrevData(){
        let prev = this.tableData.previous;
        this.tableData.previous = null
        --this.pagination.default;
        if(prev){
          let products = await axios.get(prev);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
        }
      },
      async deleteOrder(order_id){
        this.$swal({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) =>{
            if(result.isConfirmed) {
              var config = {
                  method: 'delete',
                  url: process.env.VUE_APP_SERVER_HOST+'/api/order/'+ order_id +'/',
                  xsrfHeaderName: "X-CSRFToken",
                };
                axios(config).then(() =>{
                  this.$swal('Deleted!','selected Order Details has been deleted.','success');
                  this.tableData.results = this.tableData.results.filter((obj)=>{return obj.id !== order_id;});
                }).catch((e) =>{
                  this.$swal('Something went wrong!',e.message,'danger');
                })
            } 
          })
      }
  }
}
</script>